@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* App.css */
/* Additional styling for the confirmation message and tulip image */
.App div {
  text-align: center;
}

.App img {
  max-width: 100%;
  height: auto;
}

.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
}

h2 {
  color: #ff1493; /* Deep pink for headings */
}

button {
  background-color: #ff69b4; /* Light pink */
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #ff85a2; /* Slightly lighter pink */
}

form {
  margin-top: 20px;
}

input[type="date"] {
  padding: 10px;
  margin-left: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

